import axios from "axios";
import authHeader from "./auth-header";

const axiosApi = axios.create({
  baseURL: `/api`,
  timeout: 1000,
  // headers: { "Content-Type": "application/json" },
});

export default {
  getPublicContent() {
    return axiosApi.get(`/test/all`);
  },

  getUserBoard() {
    return axiosApi.get(`/test/user`, { headers: authHeader() });
  },

  getModeratorBoard() {
    return axiosApi.get(`/test/mod`, { headers: authHeader() });
  },

  getAdminBoard() {
    return axiosApi.get(`/test/admin`, { headers: authHeader() });
  },
};

